import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OutOfStockComponent } from './out-of-stock.component';
import { Router, RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: "",
    component: OutOfStockComponent
  }
]

@NgModule({
  declarations: [OutOfStockComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    IonicModule,
    TranslateModule
  ]
})
export class OutOfStockModule { }
